<template>
  <div class="books content-container">
    <h1 class="books-title">My Books</h1>
    <div class="sort-container">
      <span>Sort by: </span>
      <button @click="cycleSortOption" class="sort-button">{{ sortOptionLabel }}</button>
    </div>
    <div v-for="section in sections" :key="section.title" class="book-section">
      <h2 class="section-title">{{ section.title }}</h2>
      <ul class="book-list">
        <li v-for="book in section.books" :key="book.isbn" class="book-item" :class="{ 'expanded': book.showDetails }">
          <div class="book-summary" @click="toggleDetails(book)">
            <div class="book-title">{{ book.title }}</div>
            <div class="book-author">{{ book.author }}</div>
            <div v-if="book.progress > 0 && book.progress < 100" class="progress-container">
              <div class="progress-bar">
                <div class="progress" :style="{ width: `${book.progress}%` }"></div>
              </div>
              <span class="progress-text">{{ book.progress }}%</span>
            </div>
            <div v-else-if="book.progress === 100" class="completion-date">
              Completed on: {{ formatDate(book.dateRead) }}
            </div>
          </div>
          <div v-if="book.showDetails" class="book-details">
            <p><strong>ISBN:</strong> {{ book.isbn }}</p>
            <p><strong>Added on:</strong> {{ formatDate(book.dateAdded) }}</p>
            <p><strong>Publisher:</strong> {{ book.publisher }} ({{ book.yearPublished }})</p>
            <p><strong>Rating:</strong> {{ book.rating.toFixed(1) }} / 5</p>
            <p><strong>Pages:</strong> {{ book.numPages }}</p>
            <p v-if="book.shelf"><strong>Shelf:</strong> {{ book.shelf }}</p>
            <img v-if="book.image" :src="book.image" :alt="book.title + ' cover'" class="book-cover">
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'

export default {
  name: 'Books',
  setup() {
    const store = useStore()
    const books = ref([])
    const sortOptions = ['title', 'author', 'progress', 'dateRead']
    const sortOptionIndex = ref(0)

    const sortOption = computed(() => sortOptions[sortOptionIndex.value])
    const sortOptionLabel = computed(() => {
      const labels = {
        title: 'Title',
        author: 'Author',
        progress: 'Progress',
        dateRead: 'Date Read'
      }
      return labels[sortOption.value]
    })

    onMounted(() => {
      store.dispatch('content/loadBooks')
      books.value = store.getters['content/getBooks'].map(book => ({
        ...book,
        showDetails: false
      }))
    })

    const formatDate = (dateString) => {
      if (!dateString) return 'Not available'
      const options = { year: 'numeric', month: 'long', day: 'numeric' }
      return new Date(dateString).toLocaleDateString(undefined, options)
    }

    const toggleDetails = (book) => {
      book.showDetails = !book.showDetails
    }

    const cycleSortOption = () => {
      sortOptionIndex.value = (sortOptionIndex.value + 1) % sortOptions.length
    }

    const sortBooks = () => {
      books.value.sort((a, b) => {
        switch (sortOption.value) {
          case 'title':
            return a.title.localeCompare(b.title)
          case 'author':
            return a.author.localeCompare(b.author)
          case 'progress':
            return b.progress - a.progress
          case 'dateRead':
            return new Date(b.dateRead || 0) - new Date(a.dateRead || 0)
          default:
            return 0
        }
      })
    }

    const sections = computed(() => {
      sortBooks()
      return [
        {
          title: 'Currently Reading',
          books: books.value.filter(book => book.progress > 0 && book.progress < 100)
        },
        {
          title: 'Want to Read',
          books: books.value.filter(book => book.progress === 0)
        },
        {
          title: 'Finished',
          books: books.value.filter(book => book.progress === 100)
        }
      ]
    })

    return {
      sections,
      sortOptionLabel,
      formatDate,
      toggleDetails,
      cycleSortOption
    }
  }
}
</script>

